body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
		'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
		'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-size: 14px;
	max-width: 1024px;
	margin: 20px auto;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

.file {
	position: relative;
	height: 30px;
	line-height: 30px;
	display: block;
	margin-left: 3px;
}

.file__icon {
	position: absolute;
	top: 3px;
}

.file__name {
	margin-left: 30px;
}

.file__size {
    display: inline-block;
    margin-left: 10px;
    text-decoration: none;
    color: #222;
}
.folder-button__button {
	border: 0;
	background: transparent;
	display: inline-block;
	height: 30px;
	line-height: 30px;
	position: relative;
	font-size: 14px;
	padding: 0;
}

.folder-button__icon {
	display: inline-block;
	height: 30px;
	line-height: 30px;
	position: absolute;
	top: 3px;
}

.folder-button__name {
	display: inline-block;
	height: 30px;
	line-height: 30px;
	margin-left: 50px;
}

.folder {
	list-style: none;
	margin: 0;
}

.folder__container--open {
	display: block;
}

.folder__container--closed {
	display: none;
}


